import SVGInliner from 'svg-inliner';
import '../styles/index.scss';
import adtGallery from './modules/adt-gallery';
import Header from './modules/header';
import Popup from './modules/popup';

const AppDefault = {
  start() {
    try {
      new SVGInliner(document.querySelectorAll(".svg-inline"));
    } catch (e) {
      console.log('err svginline ', e)
    }

    try {
      Header.init();
    } catch (e) {
      console.log('err adt header ', e)
    }

    try {
      Popup.init();
    } catch (e) {
      console.log('err popup ', e)
    }

    try {
      adtGallery.init();
    } catch (e) {
      console.log('err adt gallery slider ', e)
    }

  }

}

AppDefault.start();