const Popup = {

    init() {
        this.closeHandlers();
    },

    closeHandlers() {
        document.querySelectorAll('.popup').forEach(item => {
            item.querySelector('.js-popup-close').addEventListener('click', () => {
                item.closest('.popup').classList.remove('show');
                document.body.classList.remove('body-overflow');
            })

            item.querySelector('.js-popup-overlay').addEventListener('click', () => {
                item.closest('.popup').classList.remove('show');
                document.body.classList.remove('body-overflow');
            })
        })
    }
}

export default Popup;