const Header = {
    init() {
        const calcHeader = () => {
            if (document.querySelector('.js-header-calc')) {
                document.querySelector('.js-header-calc').style.paddingTop = parseFloat(getComputedStyle(document.querySelector('.header'), null).height.replace("px", "")) + 'px';
            }
        }
        calcHeader();

        window.addEventListener('load', () => {
            setTimeout(calcHeader, 100);
        })

        window.addEventListener('resize', () => {
            setTimeout(calcHeader, 100);
        })

        let header = document.querySelector('.header'),
            scrollPrev = 0;

        window.addEventListener('scroll', () => {
            let scrolled = window.scrollY;

            if (scrolled > 100 && scrolled > scrollPrev) {
                document.querySelector('.adt__right-content').style.top = '30px';
                header.classList.add('out');
            } else {
                document.querySelector('.adt__right-content').style.top = '140px';
                header.classList.remove('out');
            }
            scrollPrev = scrolled;
        })

        if (document.querySelector('.js-location')) {
            document.querySelector('.js-location').addEventListener('click', (e) => {
                e.currentTarget.classList.toggle('active');
            })
        }

        document.addEventListener('click', function (e) {
            e.stopPropagation();
            if (e.target.className !== 'header__location-text') {
                document.querySelector('.js-location').classList.remove('active');
            }
        })

        if (document.querySelector('.js-burger')) {
            document.querySelector('.js-burger').addEventListener('click', (e) => {
                e.currentTarget.classList.toggle('active');
                document.querySelector('.js-nav').classList.toggle('show');

                if (e.currentTarget.classList.contains('active')) {
                    document.querySelector('.contacts').style.display = 'none';
                } else {
                    document.querySelector('.contacts').style.display = 'block';
                }

                document.body.classList.toggle('body-overflow');
                document.querySelectorAll('.js-hide-el').forEach(item => {
                    item.classList.toggle('hide');
                })
            }, {
                passive: true,
            })
        }


        if (document.querySelectorAll('.js-open-search')) {
            document.querySelectorAll('.js-open-search').forEach(item => {
                item.addEventListener('click', (e) => {
                    document.querySelector('.js-search-mobile').classList.add('show');
                    document.body.classList.add('body-overflow');
                    document.querySelector('.contacts').style.display = 'none';
                }, {
                    passive: true
                })
            })
        }

        if (document.querySelector('.js-hide-search')) {
            document.querySelector('.js-hide-search').addEventListener('click', (e) => {
                document.querySelector('.js-search-mobile').classList.remove('show');
                if (!document.querySelector('.js-nav').classList.contains('show')) {
                    document.body.classList.remove('body-overflow');
                    document.querySelector('.contacts').style.display = 'block';
                } else {
                    document.querySelector('.contacts').style.display = 'none';
                }

                document.querySelectorAll('.js-search-form').forEach(item => {
                    item.reset();
                })
            }, {
                passive: true
            })
        }

        if (document.querySelectorAll('.js-remove-search').length) {
            document.querySelectorAll('.js-remove-search').forEach(item => {
                item.addEventListener('click', (e) => {
                    e.currentTarget.closest('.search__recently-item').remove();
                    if (document.querySelectorAll('.js-remove-search').length === 0) {
                        document.querySelector('.search__recently-title').style.display = 'none';
                    }
                }, {
                    passive: true
                })
            })
        }

        if (document.querySelectorAll('.search__recently-item').length) {
            document.querySelectorAll('.search__recently-item').forEach(item => {
                item.addEventListener('click', function (e) {
                    if (e.target.className === 'search__recently-result') {
                        document.querySelector('.search__input').value = e.target.textContent;
                    }
                }, {
                    passive: true
                })
            })
        }
    }
}

export default Header;