import Swiper from 'swiper';
import SwiperCore, {
    Navigation,
    Thumbs
} from 'swiper/core';
SwiperCore.use([Navigation, Thumbs]);
import 'swiper/swiper-bundle.css';

const adtGallery = {
    init() {
        const gallery = new Swiper('.js-slider-default', {
            slidesPerView: 1,
            loop: true,
            spaceBetween: 5,
            grabCursor: true,
            // autoHeight: true,
            navigation: {
                nextEl: '.default-next',
                prevEl: '.default-prev',
            },
            breakpoints: {
                320: {
                    spaceBetween: 10,
                    loop: false,
                    centeredSlides: true,
                    centeredSlidesBounds: true,
                    slidesPerView: 'auto',
                    watchSlidesVisibility: true,
                },
                1024: {
                    spaceBetween: 5,
                    loop: true,
                    centeredSlides: false,
                    centeredSlidesBounds: false,
                    slidesPerView: 1,
                    watchSlidesVisibility: false,
                }
            }
        });

        let totalSlides = null;

        if (window.matchMedia("(min-width: 1024px)").matches) {
            totalSlides = gallery.slides.length - 2;
        }

        if (window.matchMedia("(max-width: 1023px)").matches) {
            totalSlides = gallery.slides.length;
        }

        document.querySelectorAll('.js-total-gallery').forEach(item => {
            item.innerHTML = totalSlides;
        })

        gallery.on('transitionEnd', function (instance) {

            let currentCount = null;

            if (window.matchMedia("(min-width: 1024px)").matches) {
                currentCount = (instance.activeIndex - 1) % (totalSlides) + 1;
            }

            if (window.matchMedia("(max-width: 1023px)").matches) {
                currentCount = (instance.activeIndex - 1) % (totalSlides) + 2;
            }

            if (currentCount === 0) {
                document.querySelectorAll('.js-current-gallery').forEach(item => {
                    item.innerHTML = totalSlides;
                })
            } else {
                document.querySelectorAll('.js-current-gallery').forEach(item => {
                    item.innerHTML = currentCount;
                })
            }

        });


        document.querySelectorAll('.js-show-details').forEach(item => {
            item.addEventListener('click', function (event) {
                event.preventDefault();
                this.classList.toggle('active');

                let toggleContent = item.closest('.js-wrap-details').querySelector('.js-details');

                if (!toggleContent.classList.contains('show')) {
                    toggleContent.classList.add('show');
                    toggleContent.style.height = 'auto';

                    const height = toggleContent.clientHeight + 'px';

                    toggleContent.style.height = '0px';

                    setTimeout(function () {
                        toggleContent.style.height = height;
                    }, 0);
                } else {
                    toggleContent.style.height = '0px';

                    toggleContent.addEventListener('transitionend',
                        function () {
                            toggleContent.classList.remove('show');
                        }, {
                            once: true
                        });
                }
            });
        })

        if (document.querySelector('.js-hide-ads')) {
            document.querySelector('.js-hide-ads').addEventListener('click', function (event) {
                event.preventDefault();
                document.querySelector('.adt-details__btn').classList.remove('active');

                let toggleContent = document.querySelector('.ads');

                toggleContent.style.height = '0px';

                toggleContent.addEventListener('transitionend',
                    function () {
                        toggleContent.classList.remove('show');
                    }, {
                        once: true
                    });
            });
        }

        let swiper = new Swiper('.js-popup-slider', {
            spaceBetween: 10,
            autoplay: {
                delay: 400,
            },
            // autoHeight: true,
            grabCursor: true,
            navigation: {
                nextEl: '.popup-next',
                prevEl: '.popup-prev',
            },
            thumbs: {
                swiper: gallery,
            },
            breakpoints: {
                320: {
                    thumbs: {
                        swiper: null,
                    },
                },
                1024: {
                    thumbs: {
                        swiper: gallery,
                    },
                }
            }
        });

        let totalSlidesPopup = swiper.slides.length - 2;

        document.querySelectorAll('.js-total-gallery-popup').forEach(item => {
            item.innerHTML = totalSlidesPopup;
        })

        document.querySelector('.js-full-screen').addEventListener('click', () => {
            swiper.slideTo(gallery.realIndex);
            document.body.classList.add('body-overflow');
            document.querySelector('.js-popup-gallery').classList.add('show');
        })

        document.querySelector('.js-slider-default').querySelector('.swiper-wrapper').querySelectorAll('.swiper-slide').forEach(item => {
            item.addEventListener('click', () => {
                swiper.slideTo(gallery.realIndex);
                document.body.classList.add('body-overflow');
                document.querySelector('.js-popup-gallery').classList.add('show');
            })
        })

        if (document.querySelector('.js-close-contacts')) {
            document.querySelector('.js-close-contacts').addEventListener('click', () => {
                document.querySelector('.js-open-contacts').querySelector('.contacts__head').classList.remove('active');
                document.body.classList.remove('body-overflow');
                document.querySelector('.contacts-info').classList.remove('show');
            })
        }

        if (document.querySelector('.js-contacts-overlay')) {
            document.querySelector('.js-contacts-overlay').addEventListener('click', () => {
                document.querySelector('.js-open-contacts').querySelector('.contacts__head').classList.remove('active');
                document.body.classList.remove('body-overflow');
                document.querySelector('.contacts-info').classList.remove('show');
            })
        }

        if (window.matchMedia("(max-width: 1023px)").matches) {

            swiper.on('transitionEnd', function (instance) {

                gallery.slideTo(swiper.realIndex);
    
            });

            if (document.querySelector('.js-open-contacts')) {
                document.querySelector('.js-open-contacts').querySelector('.contacts__head').addEventListener('click', (e) => {
                    e.currentTarget.classList.add('active');
                    document.body.classList.add('body-overflow');
                    document.querySelector('.contacts-info').classList.add('show');
                })
            }

            new Swiper('.js-offers-slider', {
                spaceBetween: 10,
                loop: false,
                centeredSlides: true,
                centeredSlidesBounds: true,
                slidesPerView: 'auto',
                grabCursor: true,
                watchSlidesVisibility: true,
            });

            new Swiper('.js-similar-slider', {
                spaceBetween: 10,
                loop: false,
                centeredSlides: true,
                centeredSlidesBounds: true,
                slidesPerView: 'auto',
                grabCursor: true,
                watchSlidesVisibility: true,
            });
        }
    }
}

export default adtGallery;